.nav-top {
  display: block;
  height: 56px;
  background-color: #fff;
  border-color: #f0f0f0;
  min-height: 50px;
  border-bottom: 1px solid #eee;
}
.nav-top .nav-header {
  max-width: 1440px;
  margin: 0 auto;
}
.nav-top .nav-header .logo-wrapper {
  float: left;
  width: 100px;
  padding: 0 15px;
  height: 56px;
  font-size: 1.5rem;
  font-weight: 600;
  color: #2c3e50;
  letter-spacing: 0.5em;
}
.nav-top .nav-header .logo-wrapper a {
  line-height: 56px;
}
.nav-top .nav-header .center-nav {
  display: none;
}
.nav-top .nav-header .menu-btn-wrapper {
  float: right;
  margin-right: 10px;
}
.nav-top .nav-header .menu-btn-wrapper .menu-btn {
  display: block;
  padding: 0 25px;
  height: 56px;
  line-height: 56px;
  color: #F47023;
}
.nav-top .nav-header .menu-btn-wrapper .menu-btn .iconfont {
  font-size: 18px;
}
.nav-top .nav-header .menu-btn-wrapper .menu-btn:hover {
  opacity: 0.8;
}
.nav-top .nav-header .login-register-wrapper {
  float: right;
  margin: 0 10px;
  height: 56px;
  line-height: 56px;
  display: none;
}
.nav-top .nav-header .login-register-wrapper .nav-btn {
  margin-right: 15px;
}
.nav-top .nav-header .login-register-wrapper .nav-btn:focus {
  background: inherit;
  border: 1px solid #DCDFE6;
  color: #606266;
}
.nav-top .nav-header .login-register-wrapper .nav-btn:hover {
  color: #ec6149;
  border-color: #e8b5ac;
  background-color: rgba(236, 97, 73, 0.05);
}
.nav-top .nav-header .login-register-wrapper .nav-btn:active {
  border-color: #ec6149;
  color: #ec6149;
  background-color: rgba(236, 97, 73, 0.05);
}
.header-list .header-item {
  float: left;
}
.header-list .header-item .nav-link {
  display: block;
  position: relative;
  height: 56px;
  line-height: 56px;
  padding: 0 12px;
  letter-spacing: 0.1em;
  font-size: 16px;
  color: #3b4956;
}
.header-list .header-item .nav-link:after {
  position: absolute;
  left: 50%;
  margin-left: -7px;
  bottom: 1px;
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  vertical-align: middle;
  border-bottom: 7px solid #F47023;
  border-right: 7px solid transparent;
  border-left: 7px solid transparent;
  border-top: 0 dotted;
  -webkit-transform: rotate(360deg);
  transform: rotate(360deg);
  -webkit-transition: opacity 0.1s;
  transition: opacity 0.1s;
  opacity: 0;
}
.header-list .header-item .nav-link:hover {
  color: #F47023;
  background-color: #f0f0f0;
}
.header-list .header-item .nav-link:hover:after {
  opacity: 1;
}
.header-list .header-item .item-active {
  color: #F47023;
}
.header-list .header-item .search-input {
  width: 180px;
  height: 56px;
  line-height: 56px;
  margin: 0 12px;
}
.write-btn-wrapper {
  display: block;
  float: right;
  padding: 10px 0;
}
.write-btn-wrapper .write-btn {
  height: 36px;
  font-size: 14px;
  line-height: 36px;
  padding: 8px 16px;
  letter-spacing: 0.1em;
  color: #F47023;
  border-radius: 40px;
  background-color: #f0f0f0;
  border: 1px solid #dcdfe6;
}
.write-btn-wrapper .write-btn:hover {
  border-color: #d2d2d2;
  background-color: #e3e3e3;
}
.write-btn-wrapper .write-btn:active {
  border-color: #999;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
.main-container {
  margin-right: auto;
  margin-left: auto;
  padding-left: 20px;
  padding-right: 20px;
}
.blog-sidebar-wrapper {
  display: none;
}
.blog-list .blog-item {
  padding: 20px;
  border-radius: 10px;
  border: 1px dashed #f1af87;
}
.blog-list .blog-item:not(:last-child) {
  margin-bottom: 20px;
}
.version {
  display: none;
}
.nav-menu-wrapper {
  display: block;
}
.menu-btn-wrapper {
  display: block;
}
@media (min-width: 768px) {
.main-container {
    width: 750px;
}
}
@media (min-width: 992px) {
.nav-top .nav-header .center-nav {
    display: block;
}
.nav-top .nav-header .login-register-wrapper {
    display: block;
}
.nav-menu-wrapper {
    display: none;
}
.menu-btn-wrapper {
    display: none;
}
.main-container {
    width: 960px;
}
.main-container .blog-item {
    border: 0;
}
.main-container .blog-list .blog-item {
    border-radius: 0;
    padding: 20px 0 15px;
    border-bottom: 1px dashed #f1af87;
}
.main-container .blog-list .blog-item:first-child {
    border-top: 1px dashed #f1af87;
}
.main-container .blog-sidebar-wrapper {
    display: block;
}
}
@media (min-width: 1081px) {
.main-container {
    width: 960px;
}
